.home{
    display: flex;
    justify-content: center;
    margin-right: auto;
    margin-left: auto;
    max-width: 1500px;
}
.homeRow{
    display: flex;
    z-index: 1;
    margin-left: 10px;
    margin-right: 10px;
}

.homeImage{
    width: 100%;
    z-index: -1;
    margin-bottom: -200px;
    mask-image: linear-gradient(to bottom ,rgba(0,0,0,1),rgba(0,0,0,0));
}