.product{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin: 10px;
    padding: 10px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}
.productImage{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.productInfo{
    width: 100%;
    height: 100px;
    margin-bottom: 15px;
}
.productPrice{
    margin-top: 5px;
}
.productBtn{
    border : 1px solid;
    background-color: #f0c14b;
    margin-top: 10px;
    color: black;
    border-color: #a88734 #9c7e31 #846a29
}
.productRating{
display: flex;
}