.subtotal{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 300px;
    height: 150px;
    padding: 20px;
    border: 1px solid #dddddd;
    border-radius: 3px;
    background-color: #f3f3f3;
    margin-left: 25px;
}
.subtotalGift{
    display: flex;
    align-items: center;
}
.subtotalGift > input{
    margin-right: 5px;
}
.subtotal > button{
    border : 1px solid;
    width: 100%;
    border-radius: 5px;
    height: 30px;
    background-color: #f0c14b;
    margin-top: 10px;
    color: black;
    border-color: #a88734 #9c7e31 #846a29;
}